











import {Component, Prop, Vue} from "vue-property-decorator";
import ScoreRubricStore from "../ScoreRubricStore";

@Component({
    components: {}
})
export default class Collapse extends Vue {
    @Prop()
    collapsed!: boolean;

    @Prop()
    title!: string;

    get caretClass() {
        return {
            "fa-caret-down": !this.collapsed,
            "fa-caret-right": this.collapsed
        }
    }
}


